<template>
	<transition name="aboutTransition">
		<div class="p2">
			<h4></h4>
			<p class="h6">
				    Zhihu 
			</p>
		</div>
	</transition>
</template>
<script>
	export default{
		name:'About'
	}
</script>
<style scoped>
	p{
		text-indent: 1rem;
	}
	.aboutTransition-enter,.aboutTransition-leave-active{
		opacity: 0;
	}
	.aboutTransition-enter-active,.aboutTransition-leave-active{
		transition:opacity 0.5s;
	}
</style>


